<template>
	<view>
		<u-navbar title="" :is-back='false'>
			<view class="slot-wrap">
				<uni-icons type="arrowleft" @click="back" size="18" color='#343434'></uni-icons>
				<text>{{title}}</text>
				<text @click="save(inforid)" v-if="show" :class="boolean?'btnTitle':''">{{btnTitle}}</text>
				<text @click="upload(1)" v-if="idshow" class="btnTitle">{{btnTitle}}</text>
				<text @click="upload(2)" v-if="licenseshow" class="btnTitle">{{btnTitle}}</text>
			</view>
		</u-navbar>
		<view class="" v-if="show">
			<u-field
				v-model="input"
				:placeholder="placeholder"
				:error-message="errorMessage"
				label-width='0'
				:focus='focus'
			>
			</u-field>
		</view>
		<view class="inputInfor" v-if="idshow">
			<view class="imgbox">
				<text>上传身份证正面照片：</text>
				<view class="imgpic" @click.capture="uploadpic(0)">
					<image style="height: 388rpx;" :src="frontsrc" mode="aspectFit"></image>
				</view>
			</view>
			<view class="imgbox">
				<text>上传身份证反面照片：</text>
				<view class="imgpic" @click="uploadpic(1)">
					<image style="height: 388rpx;" :src="backsrc" mode="aspectFit"></image>
				</view>
			</view>
		</view>
		<view class="inputInfor" v-if="licenseshow">
			<view class="imgbox">
				<text>上传医师执照：</text>
				<view class="imgpic" @click.capture="uploadpic(2)">
					<image style="height: 388rpx;" :src="licensesrc" mode="aspectFit"></image>
				</view>
			</view>
		</view>
		<view>
			<u-toast ref="uToast" />
		</view>
		
	</view>
</template>

<script>
	import {amend} from '@/utill/api/personalInfor/amendInfor.js' 
	export default {
		data() {
			return {
				focus:true,
				title:'修改信息',
				btnTitle:"保存",
				boolean:false,
				input:'',
				placeholder:'输入姓名',
				errorMessage: '',
				inforid:'',//不同的信息修改
				show:true,//其他信息的输入
				idshow:false,//身份证找票
				licenseshow:false,//营业执照
				frontsrc:'',//正面
				backsrc:'',//反面
				frontId:'',
				backId:'',
				licensesrc:'',
				licenseId:''
			}
		},
		methods: {
			back(){
				uni.navigateBack({
					delta:1
				})
			},
			save(id){
				if(this.input==''){
					this.$refs.uToast.show({
						title: '不能为空',
					})
				}else{
					if(id==0){//修改name
						amend({
							doctorId:uni.getStorageSync('doctorId'),
							name:this.input
						}).then(res=>{
							console.log(res)
							if(res.data.code==0){
								uni.navigateTo({
									url: '/pages/PersonalInfor/PersonalInfor',
								})
							}else{
								this.$refs.uToast.show({
									title: res.data.msg
								})
							}
						}).catch(err=>{
								console.log(err)
						})
					}else if(id==1){//修改所属医院
						amend({
							doctorId:uni.getStorageSync('doctorId'),
							hospital:this.input
						}).then(res=>{
							console.log(res)
							if(res.data.code==0){
								uni.navigateTo({
									url: '/pages/PersonalInfor/PersonalInfor',
								})
							}else{
								this.$refs.uToast.show({
									title: res.data.msg
								})
							}
						}).catch(err=>{
								console.log(err)
						})
					}else if(id==2){//修改所属医院地址
						amend({
							doctorId:uni.getStorageSync('doctorId'),
							address:this.input
						}).then(res=>{
							console.log(res)
							if(res.data.code==0){
								uni.navigateTo({
									url: '/pages/PersonalInfor/PersonalInfor',
								})
							}else{
								this.$refs.uToast.show({
									title: res.data.msg
								})
							}
						}).catch(err=>{
								console.log(err)
						})
					}else if(id==3){//修改科室
						amend({
							doctorId:uni.getStorageSync('doctorId'),
							departmentName:this.input
						}).then(res=>{
							console.log(res)
							if(res.data.code==0){
								uni.navigateTo({
									url: '/pages/PersonalInfor/PersonalInfor',
								})
							}else{
								this.$refs.uToast.show({
									title: res.data.msg
								})
							}
						}).catch(err=>{
								console.log(err)
						})
					}else if(id==4){//修改职位
						amend({
							doctorId:uni.getStorageSync('doctorId'),
							position:this.input
						}).then(res=>{
							console.log(res)
							if(res.data.code==0){
								uni.navigateTo({
									url: '/pages/PersonalInfor/PersonalInfor',
								})
							}else{
								this.$refs.uToast.show({
									title: res.data.msg
								})
							}
						}).catch(err=>{
								console.log(err)
						})
					}
				}
			},
			// 选择图片
			uploadpic(index){
				if(index==0){//正面身份证照片
					uni.chooseImage({
						count: 1, //默认9
					    success: (chooseImageRes) => {
					        const tempFilePaths = chooseImageRes.tempFilePaths;
							const tempFiles=chooseImageRes.tempFiles;
							this.$uploadRequest({
								url:'/upload/images/doctor',
								filePath: tempFilePaths[0],
							}).then(res=>{
								console.log(JSON.parse(res.data))
								var data=JSON.parse(res.data)
								console.log(data)
								if(data.code==0){
									this.frontsrc=tempFilePaths[0]
									this.frontId=data.object.fileLibId
								}else{
									uni.showToast({
										title:data.msg,
										icon:'none'
									})
								}
							}).catch(err=>{
								console.log(err)
							})
					    },
						fail(e) {console.log(e)}
					});
				}else if(index==1){//反面照片
					uni.chooseImage({
						count: 1, //默认9
					    success: (chooseImageRes) => {
					        const tempFilePaths = chooseImageRes.tempFilePaths;
							const tempFiles=chooseImageRes.tempFiles;
							this.$uploadRequest({
								url:'/upload/images/doctor',
								filePath: tempFilePaths[0],
							}).then(res=>{
								console.log(JSON.parse(res.data))
								var data=JSON.parse(res.data)
								console.log(data)
								if(data.code==0){
									this.backsrc=tempFilePaths[0]
									this.backId=data.object.fileLibId
								}else{
									uni.showToast({
										title:data.msg,
										icon:'none'
									})
								}
							}).catch(err=>{
								console.log(err)
							})
					    },
						fail(e) {console.log(e)}
					});
				}else if(index==2){
					uni.chooseImage({
						count: 1, //默认9
					    success: (chooseImageRes) => {
					        const tempFilePaths = chooseImageRes.tempFilePaths;
							this.$uploadRequest({
								url:'/upload/images/doctor',
								filePath: tempFilePaths[0],
							}).then(res=>{
								console.log(JSON.parse(res.data))
								var data=JSON.parse(res.data)
								console.log(data)
								if(data.code==0){
									this.licensesrc=tempFilePaths[0]
									this.licenseId=data.object.fileLibId
								}else{
									uni.showToast({
										title:data.msg,
										icon:'none'
									})
								}
							}).catch(err=>{
								console.log(err)
							})
					    },
						fail(e) {console.log(e)}
					});
				}
			},
			// 上传到服务器
			upload(index){
				if(index==1){//上传身份证
					if(!this.frontId&&!this.backId){
						uni.showToast({
							title:'请上传身份证',
							icon:'none'
						})
						return;
					}
					amend({
						doctorId:uni.getStorageSync('doctorId'),
						iDcardPicture:this.frontId,
						iDcardPictureBack:this.backId
					}).then(res=>{
						console.log(res)
						if(res.data.code==0){
							uni.navigateTo({
								url:'/pages/PersonalInfor/PersonalInfor',
							})
						}else{
							uni.showToast({
								title:res.data.msg,
								icon:'none'
							})
						}
					}).catch(err=>{
							console.log(err)
					})
				}else if(index==2){//上传执照
					if(!this.licenseId){
						uni.showToast({
							title:'请上传医生执照',
							icon:'none'
						})
						return;
					}
					amend({
						doctorId:uni.getStorageSync('doctorId'),
						doctorLicense:this.licenseId,
					}).then(res=>{
						console.log(res)
						if(res.data.code==0){
							uni.navigateTo({
								url:'/pages/PersonalInfor/PersonalInfor',
							})
						}else{
							uni.showToast({
								title:res.data.msg,
								icon:'none'
							})
						}
					}).catch(err=>{
							console.log(err)
					})
				}
				
			},
		},
		watch:{
			input (newValue,oldValue){
				console.log(newValue)
				console.log(oldValue)
				if(newValue!=oldValue){
					this.boolean=true
				}
			}
		},
		onLoad(options) {
			console.log(options)
			this.title=options.pageTitle
			this.inforid=options.pageId
			// this.inforid=6
			if(this.inforid==0){
				this.placeholder='输入姓名'
			}else if(this.inforid==1){
				this.placeholder='输入所属医院'
			}else if(this.inforid==2){
				this.placeholder='输入医院地址'
			}else if(this.inforid==3){
				this.placeholder='输入科室'
			}else if(this.inforid==4){
				this.placeholder='输入职位'
			}else if(this.inforid==5){//上传身份证图片
				this.show=false
				this.idshow=true
				this.btnTitle='上传'
				if(options.backSrc){
					this.backsrc=options.backSrc
				}
				if(options.frontSrc){
					this.frontsrc=options.frontSrc
				}
			}else if(this.inforid==6){//上传医生执照
				this.show=false
				this.idshow=false
				this.licenseshow=true
				this.btnTitle='上传执照'
				if(options.licenseSrc){
					this.licensesrc=options.licenseSrc
				}
			}
		}
	}
</script>

<style scoped>
	page{
		background-color: #FFFFFF;
	}
	.slot-wrap{
		flex:1;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 30rpx;
	}
	.slot-wrap uni-text:nth-child(2){
		color: #000000;
		font-size: 30rpx;
		font-family: PingFang-SC-Bold;
		font-weight: bold;
	}
	.slot-wrap uni-text:nth-child(3){
		color: #909090;
		font-size: 28rpx;
	}
	.btnTitle{
		color: #000000!important;
	}
	.inputInfor{
		padding: 20rpx 30rpx;
	}
	.imgbox{
		margin-bottom: 50rpx;
	}
	.imgpic{
		background: url(../../static/image/camerabg.png) no-repeat center;
		background-size: 80rpx; width: 100%;height: 388rpx;
		border:1px solid #EEEEEE;
		line-height: 388rpx;
		text-align: center;
		margin-top: 20rpx;
	}
</style>
